@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

#root {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.userForm {
  width: 80%;
  max-width: 400px;
  height: auto;
  background-color: aquamarine;
  padding: 2rem;
  border-radius: 7px;
  box-shadow: 0px 0px 15px black;
  border: 1px solid rgba(0, 0, 0, 0.374);
  position: relative;
}

.inputs {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.inputs input {
  height: 40px;
  border: 1px solid black;
  outline: none;
  border-radius: 7px;
  padding-left: 2rem;
  font-size: large;
}

.buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.login {
  padding: .5rem 1rem;
  background-color: cadetblue;
  border: 1px solid black;
  border-radius: 7px;
  cursor: pointer;
  color: whitesmoke;
}

.login:hover {
  background-color: aqua;
  color: black;
}

.label {
  font-size: 1.2rem;
}

.error {
  color: red;
  margin-left: 5px;
}

/* chat */
h2 {
  text-align: center;
}

#chat {
  width: 100%;
  margin-top: 50px;
  margin-bottom: 10px;
  height: 500px;
  max-height: 500px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  overflow-y: auto;
  padding: .5rem 0;
}

.msg {
  display: flex;
  width: 75%;
  height: auto;
  background-color: beige;
  padding: .6rem;
  border-radius: 7px;
}

.right {
  background-color: rgb(166, 101, 226);
  margin-left: auto;
}

.sendMsg {
  display: flex;
  width: 100%;
  gap: 10px;
}

.sendMsg input {
  width: 80%;
  height: 30px;
  border-radius: 7px;
  border: 1px solid black;
  outline: none;
  padding-left: 20px;
}

.sendMsg input:focus {
  border: 2px solid black;
}

.sendMsg button {
  width: 20%;
  height: 30px;
  border-radius: 7px;
  border: 1px solid black;
  cursor: pointer;
}

.sendMsg button:hover {
  background-color: rgb(107, 107, 107);
}

.userName {
  color: darkred;
}

.userMsg {
  color: rgb(81, 81, 81);
}

#user {
  position: absolute;
  top: 15px;
  left: 15px;
}

#reactChat {
  position: absolute;
  top: 15px;
  right: 15px;
}




::-webkit-scrollbar {
  width: 7px;
  border-radius: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
  border-radius: 5px;
}


/* loader */
#loader {
  top: 1%;
  left: 47%;
  position: absolute;
  width: 50px;
  z-index: 1000;
  animation: loader 400ms infinite;
  user-select: none;
  pointer-events: none;
}
@keyframes loader {
  0% {
    transform: rotateZ(0deg)
  }
  30% {
    transform: rotateZ(180deg)
  }
  60% {
    transform: rotateZ(250deg)
  }
  100% {
    transform: rotateZ(360deg)
  }
}
.empty{
  text-align: center;
  color: crimson;
}
.welcome{
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: bolder;
  color: white;
  -webkit-text-stroke: 1px black;
  text-shadow: 0px 0px 10px white;
}
.close{
  position: absolute;
  right: 0;
  top: 0;
  font-size: 1rem;
  border-radius: 7px;
  border: none;
  width: 20px;
  cursor: pointer;
  background-color: aquamarine;
}
.close:hover{
  color: rgb(255, 0, 0);
}